/* Header
------------------------------------------------------------*/
.c-header {
  max-height: 70px;
  height: 100%;
  position: fixed;
  z-index: 999;
  top: 0;
  width: 100%;
  padding: 5px 20px;
  background-color: #317e35;
}

@media screen and (max-width: 767px) {
  .c-header {
    padding: 0;
    max-height: none;
    height: auto;
  }
}

.c-header__menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

@media screen and (max-width: 767px) {
  .c-header__menu {
    flex-wrap: wrap;
  }
}

.c-header__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: end;
}

.c-header__listItem {
  margin-left: 20px;
  text-transform: uppercase;
  font-size: 1.7rem;
  font-weight: bold;
  color: #ffff;
}

@media only screen and (max-width: 1024px) {
  .c-header__listItem {
    margin-left: 15px;
  }
}

@media screen and (max-width: 767px) {
  .c-header__listItem {
    font-size: 1.6rem;
  }
}

.c-header__listItem a:hover {
  text-decoration: underline;
  opacity: 0.6;
}

@media screen and (max-width: 767px) {
  .c-header__listItem a:hover {
    text-decoration: none;
    opacity: 1;
  }
}

.c-header__listItem .active {
  text-decoration: underline;
  opacity: 0.6;
}

@media screen and (max-width: 767px) {
  .c-header__listItem .active {
    text-decoration: none;
    opacity: 1;
  }
}

.c-header__nav1 {
  position: absolute;
  top: 60px;
  background-color: #483b35;
  height: 100vh;
  width: 100%;
  transition: .3s;
  position: fixed;
  transform: translateX(100%);
}

.c-header__nav1 .c-header__list {
  flex-direction: column;
}

.c-header__nav1 .c-header__listItem {
  margin-left: 0;
  line-height: 1.5;
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
}

.c-header__nav1 .c-header__listItem a {
  padding: 13px 20px 12px;
  display: block;
}

.c-header__nav1.is-show {
  transform: translateX(0);
}

.c-header .is-menu {
  position: absolute;
  right: 0px;
}

.c-header .is-menu img:last-child {
  display: none;
}

.c-header .is-menu.is-active img:first-child {
  display: none;
}

.c-header .is-menu.is-active img:last-child {
  display: block;
}

/*------------------------------------------------------------
footer
------------------------------------------------------------*/
.c-footer {
  margin-top: 100px;
  padding-bottom: 50px;
}

@media screen and (max-width: 767px) {
  .c-footer {
    margin-top: 50px;
  }
}

.c-footer__list {
  display: flex;
  flex-wrap: wrap;
}

.c-footer__listItem {
  width: 25%;
  font-size: 1.6rem;
  font-weight: bold;
  margin-top: 10px;
  line-height: 1.5;
}

@media screen and (max-width: 767px) {
  .c-footer__listItem {
    width: 100%;
    padding-left: 20px;
    margin-top: 30px;
  }
}

.c-footer__listItem:not(:first-child) {
  padding-left: 40px;
}

@media screen and (max-width: 767px) {
  .c-footer__listItem:not(:first-child) {
    padding-left: 20px;
  }
}

.c-footer__listItem:not(:first-child) .c-footer__listChildItem i {
  color: #db483d;
}

.c-footer__listChild .c-footer__listChildItem:first-child {
