.c-btn1 {
  background: none;
  border: 2px solid #2fa535;
  border-radius: 5px;
  display: inline-block;
  font-size: 1.7rem;
  font-weight: bold;
  padding: 20px 70px;
  position: relative;
  text-transform: uppercase;
  color: #2fa535;
}

.c-btn1:after {
  height: 100%;
  left: 0;
  top: 0;
  width: 0;
  transition: all 0.3s;
}

.c-btn1::before, .c-btn1::after {
  background: #2fa535;
  content: '';
  position: absolute;
  z-index: -1;
}

.c-btn1:hover {
  color: #ffff;
}

.c-btn1:hover:after {
  width: 100%;
}

